// =============================================================================
// SMF Probe Status API
// =============================================================================
// GET /api/v1/smf/probes/status
// Returns whether probes are enabled and their configuration

import { NextResponse } from 'next/server';
import { PROBE_CONFIG } from '@/integrations/x402/facilitators/facilitator-probes';

export const dynamic = 'force-dynamic';

export async function GET() {
  const env = process.env.NODE_ENV || 'development';
  const enableProbes = process.env.ENABLE_FACILITATOR_PROBES === 'true';
  const isProduction = env === 'production';
  const isTest = env === 'test';
  const nextRuntime = process.env.NEXT_RUNTIME || 'unknown';
  
  // Determine probe status
  const probesEnabled = isProduction && enableProbes && !isTest;
  
  // Check if we can actually load dependencies
  let dependenciesAvailable = false;
  let dependencyError: string | null = null;
  try {
    await import('@coinbase/cdp-sdk/auth');
    await import('viem');
    dependenciesAvailable = true;
  } catch (error) {
    dependencyError = error instanceof Error ? error.message : 'Unknown error';
  }
  
  // Check recent probe activity (last hour)
  let recentProbeCount = 0;
  try {
    const { getDb } = await import('@/db/client');
    const db = getDb();
    const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
    
    if (isPostgres) {
      const oneHourAgo = new Date(Date.now() - 60 * 60 * 1000);
      const result = await (db as any).pool.query(
        `SELECT COUNT(*) as count FROM route_attempts WHERE is_probe = true AND created_at >= $1`,
        [oneHourAgo.toISOString()]
      );
      recentProbeCount = parseInt(result.rows[0].count, 10);
    }
  } catch (error) {
    // Database check failed, but don't fail the endpoint
  }
  
  return NextResponse.json({
    enabled: probesEnabled,
    running: probesEnabled && dependenciesAvailable && recentProbeCount > 0,
    environment: {
      nodeEnv: env,
      isProduction,
      isTest,
      nextRuntime,
      enableProbesFlag: enableProbes,
    },
    dependencies: {
      available: dependenciesAvailable,
      error: dependencyError,
    },
    activity: {
      recentProbeCount,
      lastHour: recentProbeCount,
      hasActivity: recentProbeCount > 0,
    },
    configuration: probesEnabled ? {
      probeCount: PROBE_CONFIG.length,
      probes: PROBE_CONFIG.map((config) => ({
        facilitatorId: config.facilitatorId,
        network: config.network,
        token: config.token,
        intervalSeconds: config.intervalMs / 1000,
        intervalMs: config.intervalMs,
      })),
    } : null,
    message: probesEnabled
      ? dependenciesAvailable
        ? recentProbeCount > 0
          ? 'Probes are enabled and running (detected recent activity)'
          : 'Probes are enabled but no recent activity detected - check server logs'
        : 'Probes enabled but dependencies unavailable'
      : isTest
      ? 'Probes disabled: test environment'
      : !isProduction
      ? 'Probes disabled: not in production environment'
      : !enableProbes
      ? 'Probes disabled: ENABLE_FACILITATOR_PROBES not set to "true"'
      : 'Probes disabled: unknown reason',
    troubleshooting: {
      steps: [
        !isProduction ? 'Set NODE_ENV=production in Vercel' : null,
        !enableProbes ? 'Set ENABLE_FACILITATOR_PROBES=true in Vercel environment variables' : null,
        !dependenciesAvailable ? `Install missing dependencies: ${dependencyError}` : null,
        probesEnabled && !recentProbeCount ? 'Check server startup logs for probe initialization errors' : null,
        'Ensure instrumentation.ts is running (should see [SMF] logs on startup)',
      ].filter(Boolean),
    },
    timestamp: new Date().toISOString(),
  });
}

